.uploads-main {
  margin: 10px;
}
.uploads-images-cover {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-row-gap: 10px;
  grid-column-gap: 10px;
}
.uploads-images-cover img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  transition: 0.2s;
  cursor: pointer;
}
.uploads-images-cover img:hover {
  box-shadow: 5px 10px 15px -10px black;
  transform: scale(1.01);
}
.image-overlay,
.upload-overlay {
  position: absolute;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.918);
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
}

.image-overlay button {
  position: absolute;
  z-index: 101;
  top: 15px;
  right: 40px;
  background: none;
  border: none;
  color: white;
  font-size: 20px;
  font-weight: 800;
  border-radius: 200px;
  background: rgb(150, 59, 255);
  transition: 0.5s;
  cursor: pointer;
}
.cross-btn {
  position: absolute;
  z-index: 101;
  top: 320px;
  right: 550px;
  background: none;
  border: none;
  color: white;
  font-size: 20px;
  font-weight: 800;
  border-radius: 200px;
  background: rgb(150, 59, 255);
  transition: 0.5s;
  cursor: pointer;
}
.image-overlay button:hover {
  transform: rotateZ(360deg);
  background: rgb(255, 98, 59);
}
.cross-btn:hover {
  transform: rotateZ(360deg);
  background: rgb(255, 98, 59);
}
.uploads-selected-image {
  width: 90%;
  height: 90%;
}
.uploader-main {
  background: white;
  padding: 20px;
  border-radius: 10px;
  border: 1px solid rgb(255, 0, 212);
  box-shadow: 0px 0px 20px -10px rgb(255, 0, 212);
}
.uploader-form button {
  background: rgb(247, 0, 255);
  border: none;
  padding: 5px 10px;
  color: white;
  border-radius: 50px;
  transition: 0.3s;
}
.uploader-form button:hover {
  background: rgb(192, 0, 199);
}
.upload-btn {
  position: fixed;
  bottom: 30px;
  right: 30px;
  background: rgb(0, 149, 194);
  color: white;
  padding: 10px;
  border-radius: 50px;
  border: none;
  z-index: 10000;
  transition: 0.2s ease;
  cursor: pointer;
}
.upload-btn:hover {
  background: rgb(52, 174, 255);
}
