.home-main {
  margin: 10px;
}
.home-images-cover {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-row-gap: 10px;
  grid-column-gap: 10px;
}
.home-images-cover img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  transition: 0.2s;
  cursor: pointer;
}
.home-images-cover img:hover {
  box-shadow: 5px 10px 15px -10px black;
  transform: scale(1.01);
}
.image-overlay {
  position: absolute;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.918);
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
}

.image-overlay button {
  position: absolute;
  z-index: 101;
  top: 15px;
  right: 40px;
  background: none;
  border: none;
  color: white;
  font-size: 20px;
  font-weight: 800;
  border-radius: 200px;
  background: rgb(150, 59, 255);
  transition: 0.5s;
  cursor: pointer;
}
.image-overlay button:hover {
  transform: rotateZ(360deg);
  background: rgb(255, 98, 59);

}
.home-selected-image {
  width: 90%;
  height: 90%;
}
