.login-main {
  width: 100%;
  height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login-main form {
  width: 400px;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid rgba(0, 0, 0, 0.459);
  border-radius: 10px;
  justify-content: center;
  padding: 50px 10px;
  box-shadow: 0px 0px 15px -5px gray;
}
.login-content {
  display: flex;
  flex-direction: column;
  justify-content: start;
  margin: 5px;
}

.login-main form button {
  background: rgb(174, 0, 255);
  color: white;
  font-weight: 700;
  padding: 8px 25px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}
.login-main form button:hover {
  background: rgb(207, 107, 253);
}
