* {
  font-family: "Fira Sans", sans-serif;
}
.styled-btn {
  margin: 10px;
  background: none;
  border: none;
  transition: 0.2s ease;
  padding: 10px 20px;
  border-radius: 50px;
  font-size: 15px;
}
.styled-btn:hover {
  background: rgba(218, 218, 218, 0.829);
}
.styled-input {
  background-color: rgba(219, 219, 219, 0.486);
  border: 2px solid rgba(0, 0, 0, 0.363);
  border-radius: 5px;
  padding: 8px 20px;
  margin: 10px 0px 10px 0px;
}
.styled-input:hover {
  background-color: rgba(219, 219, 219, 0.849);
}
